import React, { useRef, useEffect } from 'react'
import Task from './Task'
import PriorityInputRange from './PriorityInputRange'
import Icon from './Icon'

const TaskFormSimple = ({xc, handleCloseForm}) => {
    console.log('render TaskFormSimple')
    function createTask(event) {
        event.preventDefault()
        const taskName = event.target.elements.TaskName_Input.value
        const startDate = event.target.elements.StartDate_Input.value
        const dueDate = event.target.elements.DueDate_Input.value
        const priority = event.target.elements.Priority_Input.value

        const newTask = new Task({taskName, startDate, dueDate, priority})
        xc.add_task(newTask)
        handleCloseForm()
    }
    const TaskName_Input_Ref = useRef(null)
    const Submit_or_Cancel_Ref = useRef(null)
    useEffect(() => {
        // Set focus on the element after it's been rendered
        TaskName_Input_Ref.current.scrollIntoView({ block: 'start', behavior: 'smooth' })
        TaskName_Input_Ref.current.focus()
        Submit_or_Cancel_Ref.current.scrollIntoView({ block: 'start', behavior: 'smooth' })
      }, [])

    return (
        <div className="card modal-card-for-form" >
            <form onSubmit={createTask} id='taskFormSimple'>
                <div className="card-header">
                    <h2>Set a new task</h2>
                </div>
                <div className="card-body">
                    <input type="text" id="TaskName_Input" name="TaskName_Input" required={true} placeholder='New Task Name' className="form-control" ref={TaskName_Input_Ref}/>
                    <br/>
                    <div className='wrap-range-and-dates'>
                        <PriorityInputRange />
                        <div className='wrap-dates'>
                            <label htmlFor="StartDate_Input" className="form-label">Start Date:</label><br/>
                            <input type="datetime-local" id="StartDate_Input" name="StartDate_Input" className="form-control"/>
                            <br/>
                            <label htmlFor="DueDate_Input" className="form-label">Due Date:</label><br/>
                            <input type="datetime-local" id="DueDate_Input" name="DueDate_Input" className="form-control"/>
                            <br/>
                        </div>
                    </div>
                </div>
                <div className="card-footer text-body-secondary">
                    <div ref={Submit_or_Cancel_Ref} className='Submit_or_Cancel_container'>
                        <button type="submit" className="btn btn-lg btn-success">
                            <Icon i={'bi-file-earmark-plus'}/> Create
                        </button>
                        <button type="button" className="btn btn-lg btn-secondary" onClick={(event)=>{
                            event.preventDefault()
                            handleCloseForm()
                        }}><Icon i={'bi-x-lg'}/> Cancel</button>
                    </div>
                </div>
            </form>
        </div>
    )
}

export default TaskFormSimple;