import React, {useState} from 'react'
import LoadingSpinner from './LoadingSpinner'
import ItemInList from './ItemInList'

export default function ListView({xc}) {

    console.log('render ListView')

    const format = (map) => {
        let formatted = []
        map.forEach((value, key)=>{
            const task_with_key = value
            task_with_key.key = key
            formatted.push(
                <ItemInList t={task_with_key}/>
            )
        })
        return formatted
    }

    const [content, setContent] = useState()
    if (!content) {
        xc.get_all_tasks()
        .then((results) => {
            setContent(format(results))
        })
    }
    
    
    return (
        <>
            {content? content : <LoadingSpinner/>}
        </>
    )
}