import React from 'react'

export default function ItemInList({t}) {
    // console.log('render item in list')
    // console.log(t)
    return (
        <div key={t.key}>
            <div>
                {t.taskName}
            </div>
            <div>
                {t.dueDate.toDate().toString()}
            </div>
        </div>
    )
}